.form {
  width: 510px;
  height: 670px;
}

.form__title {
  font-size: 20px;
  line-height: 30px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  font-weight: 600;
  margin-bottom: 32px;
}

.form__fields {
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
}

.form__field {
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  margin-bottom: 32px;
}